// 0.1+0.20.3
export function fixDecimal(value, places = 15) {
  const multiple = Math.pow(10, places);
  return Math.round(value * multiple) / multiple;
}
// offset
export function ratioOffset(v, k, t) {
  if (v <= (1 - k) / (2 * k)) {
    return -1 / 2;
  } else if (v >= (1 + k - 2 * t) / (2 * k)) {
    return 1 / 2;
  } else {
    return (v - (1 - t) / (2 * k)) / (1 - t / k);
  }
}
// aa
export function effectuate(fn, ...args) {
  return typeof fn === 'function' ? fn(...args) : fn;
}
// vminmax
export function isBetween(v, [min, max]) {
  return min <= v && v <= max;
}
// vminmaxvminmax
export function between(v, [min, max], _) {
  return Math.max(Math.min(v, max), min);
}